import axios from 'axios';

const axiosItem = axios.create({
  baseURL: 'https://social-network.samuraijs.com/api/1.0/',
  withCredentials: true,
  headers: {
    "API-KEY": 'aedfc283-ee1f-4976-82a9-ae4ad2803260',
  }
});


export const profileApi = {

  async getProfile(userId) {
   return axiosItem.get(`profile/${userId}`);
 },

 async getStatus(userId) {
   return axiosItem.get(`profile/status/${userId}`);
 },

 async updateStatus(status) {
   return axiosItem.put(`profile/status/`, {status: status} );
 }

}


export const usersApi = {
   async getUsers(pageNum = 1, pageSize = 5) {
    return axiosItem.get(`users?page=${pageNum}&count=${pageSize}`).then( response => response.data );
  },

  async follow(userItem) {
    return axiosItem.post(`follow/${userItem.id}`);
  },

  async unfollow(userItem) {
    return axiosItem.delete(`follow/${userItem.id}`);
  },

  async getProfile(userId) {
    console.warn('Obsolete method. Please, use profileApi.getProfile()');
    return profileApi.getProfile(userId);
  }
}


export const authApi = {
  async me() {
    return axiosItem.get(`auth/me`);
  },
  async login(email, password, rememberMe = false) {
    return axiosItem.post(`auth/login`, {email, password, rememberMe});
  },
  async logout() {
    return axiosItem.delete(`auth/login`);
  },


}