import { usersApi } from '../api/api';
import { updateObjectInArray } from '../utils/helpers';

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_IN_PROGRESS = 'TOGGLE_IS_FOLLOWING_IN_PROGRESS';

const initialState = {
  users: [],
  pageSize: 5,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: false,
  isFollowingInProgress: [],
};


export const usersReducer = (state = initialState, action) => {

  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {followed: true}),
      }

    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {followed: false}),
      }

    case SET_USERS:
      return { ...state, users: action.users }

    case SET_CURRENT_PAGE:
      return { ...state, currentPage: action.currentPage }

    case SET_TOTAL_USERS_COUNT:
      return { ...state, totalUsersCount: action.totalUsersCount }

    case TOGGLE_IS_FETCHING:
      return { ...state, isFetching: !state.isFetching }

    case TOGGLE_IS_FOLLOWING_IN_PROGRESS:
      if (action.isFetching) {
        return { ...state, isFollowingInProgress: [...state.isFollowingInProgress, action.userId] }
      }
      return { 
        ...state, 
        isFollowingInProgress: state.isFollowingInProgress.filter( userId => userId !== action.userId ) 
      }

    default:
      return state;
  }
}

export const followSuccess = (userId) => (
  {
    type: FOLLOW,
    userId
  }
)

export const unfollowSuccess = (userId) => (
  {
    type: UNFOLLOW,
    userId
  }
)

export const setUsers = (users) => (
  {
    type: SET_USERS,
    users
  }
)

export const setCurrentPage = (pageNum) => (
  {
    type: SET_CURRENT_PAGE,
    currentPage: pageNum
  }
)

export const setTotalUsersCount = (totalUsersCount) => (
  {
    type: SET_TOTAL_USERS_COUNT,
    totalUsersCount
  }
)

export const toggleIsFetching = () => (
  {
    type: TOGGLE_IS_FETCHING
  }
)

export const toggleIsFollowingInProgress = (userId, isFetching) => (
  {
    type: TOGGLE_IS_FOLLOWING_IN_PROGRESS,
    userId,
    isFetching
  }
)


export const getUsers = (currentPage, pageSize) => {
  return async (dispatch) => {
    dispatch( toggleIsFetching() );
    dispatch( setCurrentPage(parseInt(currentPage)) );
    
    const data = await usersApi.getUsers(currentPage, pageSize);
    const totalUsers = data.totalCount;
    dispatch( setUsers(data.items) );
    dispatch( setTotalUsersCount(totalUsers) );
    dispatch( toggleIsFetching() );
  }
} 

export const followUnfollowFlow = async (dispatch, userItem, apiMethod, actionCreator ) => {
  dispatch( toggleIsFollowingInProgress(userItem.id, true) );

  const response = await apiMethod(userItem);
  if (response.data.resultCode === 0) {
    dispatch( actionCreator(userItem.id) );
  }
  dispatch( toggleIsFollowingInProgress(userItem.id, false) );
}

export const follow = (userItem) => {
  return async (dispatch) => {
    dispatch( toggleIsFollowingInProgress(userItem.id, true) );
    const apiMethod = usersApi.follow.bind(usersApi);
    const actionCreator = followSuccess;

    followUnfollowFlow(dispatch, userItem, apiMethod, actionCreator);
  }
}

export const unfollow = (userItem) => {
  return async (dispatch) => {
    dispatch( toggleIsFollowingInProgress(userItem.id, true) );

    const apiMethod = usersApi.unfollow.bind(usersApi);
    const actionCreator = unfollowSuccess;

    followUnfollowFlow(dispatch, userItem, apiMethod, actionCreator);
  }
}

