import css from './navbar.module.css';
import {NavLink} from 'react-router-dom';
import FriendsContainer from './friends/FriendsContainer';


const Navbar = () => {
  return (
      <nav className={css.navbar}>
        <ul className={css.list}>
          <li className={css.listItem}>
            <NavLink to='/profile' activeClassName={css.active} className={css.link}>
              Profile
            </NavLink>
          </li>
          <li className={css.listItem}>
            <NavLink to='/dialogs' activeClassName={css.active}  className={css.link}>
              Messages
            </NavLink>
          </li>
          <li className={css.listItem}>
            <NavLink to='/news' activeClassName={css.active}  className={css.link}>
              News
            </NavLink>
          </li>
          <li className={css.listItem}>
            <NavLink to='/users' activeClassName={css.active}  className={css.link}>
              People
            </NavLink>
          </li>
        </ul>
        <FriendsContainer />
      </nav>
  )
}

export default Navbar;