import { NavLink } from 'react-router-dom';
import css from './users.module.css';
import avatar from '../../../img/icon-user.png';
import { Paginator } from '../../common/paginator/Paginator';


export const Users = (props) => {

  return <div>
    <Paginator
      totalUsersCount={props.totalUsersCount}
      pageSize={props.pageSize}
      currentPage={props.currentPage}
      onPageNumClick={props.onPageNumClick}
    />
    {
      props.users.map(userItem => (
        <div key={userItem.id} className={css.item}>
          <div className={css.iconBox}>
            <NavLink to={`/profile/${userItem.id}`}>
              <img src={userItem.photos.small || avatar} alt="" className={css.icon} />
            </NavLink>
            {
              userItem.followed ?
                <button disabled={props.isFollowingInProgress.some(id => id === userItem.id)}
                  className={css.btn} onClick={() => props.unfollow(userItem)}>Unfollow</button> :
                <button disabled={props.isFollowingInProgress.some(id => id === userItem.id)}
                  className={css.btn} onClick={() => props.follow(userItem)}>Follow</button>
            }
          </div>
          <div className={css.info}>
            <p className={css.fullname}>{userItem.name}</p>
            <p className={css.location}>
              userItem.location.city, userItem.location.country
            </p>
            <p className={css.status}>
              {userItem.status ? `«‎${userItem.status}»` : ''}
            </p>
          </div>
        </div>
      ))
    }
  </div>
}