import { Form, Field } from 'react-final-form';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { login } from '../../redux/authReducer';


const LoginForm = (props) => {
  if (props.isAuth) return <Redirect to={'/profile'} />

  const onSubmit = (values) => {
    props.login(values.login, values.password, values.rememberMe);
  }

  return <Form
    onSubmit={onSubmit}
    render={({ handleSubmit, form }) => (
      <form onSubmit={handleSubmit}>
        <div>
          <Field component="input" name="login" type="text" />
        </div>
        <div>
          <Field component="input" name="password" type="password" />
        </div>
        <div>
          <Field component="input" name="rememberMe" type='checkbox' id="rememberMe" /> <label htmlFor="rememberMe">Remember me</label>
        </div>
        <div>
          <button type="submit">Submit</button>
          <button type="button" onClick={form.reset}>Reset</button>
        </div>
      </form>
    )}
  />
}

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
})


const dispatches = {
  login
}

export default connect(mapStateToProps, dispatches)(LoginForm);