import { useState } from 'react';
import css from './paginator.module.css';


export const Paginator = (props) => {

  const [pagesPart, setPagesPart] = useState(0);

  const pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
  const pagesPartsTotal = Math.floor(pagesCount / 10 - 1);
  const pagesTail = pagesCount - ((pagesPart + 1) * 10);

  const pages = [];

  let lastPageInPart = 0;
  if (pagesTail < 0) {
    lastPageInPart = pagesPart * 10 + (10 + pagesTail);
  } else {
    lastPageInPart = pagesPart * 10 + 10;
  }

  for (let i = pagesPart * 10 + 1; i <= lastPageInPart; i++) {
    pages.push(i);
  }

  const handleNextPagesPart = () => {
    setPagesPart(pagesPart + 1);
    props.onPageNumClick((pagesPart + 1) * 10 + 1);
  }

  const handlePrevPagesPart = () => {
    setPagesPart(pagesPart - 1);
    props.onPageNumClick((pagesPart - 1) * 10 + 1);
  }

  let pagesElements = null;
  if (pages.length) {
    pagesElements = pages.map((pageItem, index) => (
      <span key={index} onClick={() => props.onPageNumClick(parseInt(pageItem))} className={
        props.currentPage === parseInt(pageItem) ? css.activePage : ''
      }>{pageItem}</span>
    ))
  }


  return <div className={css.pageNavi}>
    {pagesPart > 0 ?
      <button onClick={handlePrevPagesPart} className={css.prevBtn}>
        {(pagesPart - 1) * 10 + 1}...{(pagesPart - 1) * 10 + 10}
      </button> :
      ''}
    {pagesElements}
    {pagesPart < pagesPartsTotal ?
      <button onClick={handleNextPagesPart} className={css.nextBtn}>
        {(pagesPart + 1) * 10 + 1}...{(pagesPart + 1) * 10 + 10}
      </button> : ''}
    {pagesTail > 0 && pagesTail < 10 ?
      <button onClick={handleNextPagesPart} className={css.nextBtn}>
        {(pagesPart + 1) * 10 + 1}...{(pagesPart + 1) * 10 + pagesTail}
      </button> : ''}
  </div>
}