const UPDATE_NEW_MESSAGE_BODY = 'UPDATE-NEW-MESSAGE-BODY';
const SEND_NEW_MESSAGE = 'SEND-NEW-MESSAGE';

const initialState = {
  users: [
    { id: 0, username: 'Andrey' },
    { id: 1, username: 'John' },
    { id: 2, username: 'Vikovka' },
  ],
  messages: [
    { id: 0, message: 'Hello! How are you?' },
    { id: 1, message: 'Have a nice day!' },
    { id: 2, message: 'How old are you man?' },
  ],
  newMessageBody: '',
};

export const messagesReducer = (state = initialState, action) => {
  const stateCopy = { ...state };

  switch (action.type) {
    case UPDATE_NEW_MESSAGE_BODY:
      stateCopy.newMessageBody = action.messageBody;
      return stateCopy;

    case SEND_NEW_MESSAGE:
      stateCopy.messages = [...state.messages];

      const messagesTotal = stateCopy.messages.length;
      const newMessage =
      {
        id: messagesTotal,
        message: stateCopy.newMessageBody
      };
      stateCopy.newMessageBody = '';
      stateCopy.messages.push(newMessage);
      return stateCopy;

    default:
      return state;
  }
}

export const sendMessageCreator = () => (
  {
    type: SEND_NEW_MESSAGE
  }
)

export const updateNewMessageBodyCreator = (body) => (
  {
    type: UPDATE_NEW_MESSAGE_BODY,
    messageBody: body
  }
)