import { usersApi, profileApi } from '../../src/api/api';

const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS';
const DELETE_POST = 'DELETE_POST';

const initialState = {
  posts: [
    { id: 1, message: 'Hello! My name is Alex!', likesCount: 12 },
    { id: 2, message: 'What\'s up! Here I am...', likesCount: 3 },
    { id: 3, message: 'Babe, whose your daddy? Not me))', likesCount: 3 },
  ],
  newPostText: '',
  profile: null,
  status: ''
};

export const profileReducer = (state = initialState, action) => {
  const stateCopy = { ...state };

  switch (action.type) {
    case ADD_POST:
      const newPost = {
        id: stateCopy.posts.length + 1,
        message: stateCopy.newPostText,
        likesCount: 0
      }
      stateCopy.posts.push(newPost);
      stateCopy.newPostText = '';
      return stateCopy;
    
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter( postItem => postItem.id !== action.postId )
      }

    case UPDATE_NEW_POST_TEXT:
      stateCopy.newPostText = action.newText;
      return stateCopy;

    case SET_USER_PROFILE:
      return { ...state, profile: action.profile };

    case SET_USER_STATUS:
      return { ...state, status: action.status };

    default:
      return state;
  }

}

export const updateNewPostTextActionCreator = (text) => (
  {
    type: UPDATE_NEW_POST_TEXT,
    newText: text
  }
)

export const addPostActionCreator = () => (
  {
    type: ADD_POST
  }
)

export const deletePostActionCreator = (postId) => (
  {
    type: DELETE_POST,
    postId
  }
)

export const setUserProfile = (profile) => (
  {
    type: SET_USER_PROFILE,
    profile
  }
)

export const getUserProfile = (userId) => {
  return async (dispatch) => {
    const response = await usersApi.getProfile(userId);
    dispatch(setUserProfile(response.data));
  }
}

export const setUserStatus = (status) => (
  {
    type: SET_USER_STATUS,
    status
  }
)

export const getUserStatus = (userId) => {
  return async (dispatch) => {
    const response = await profileApi.getStatus(userId);
    dispatch(setUserStatus(response.data));
  }
}

export const updateUserStatus = (status) => {
  return async (dispatch) => {
    const response = await profileApi.updateStatus(status);
    
    if (response.data.resultCode === 0) {
      dispatch(setUserStatus(status));
    }
  }
}

